import React, { useState } from "react";
import Link from "next/link";
import Image from "next/image";
import Auth from "./Auth";

export default function JoinButton() {
  const [showAuth, setShowAuth] = useState(false);

  const toggleAuth = () => {
    setShowAuth((prev) => !prev);
  };

  return (
    <div className="relative">
      <Link href="user/lumarclub" legacyBehavior passHref>
        <button className="btn" onClick={toggleAuth}>
          <Image
            src="/lumarClub.png"
            alt="lumar.co velas artesanales"
            width={2}
            height={2}
            className="btn-text-one transition-all duration-300 p-6"
          />
          <span className="btn-text-two">Quiero unirme!</span>
        </button>
      </Link>

      {showAuth && (
        <div className="absolute top-0 left-0 w-full h-full bg-gray-900 bg-opacity-50 flex justify-center items-center z-50">
          <div className="bg-white rounded-lg p-6 shadow-lg w-full max-w-md">
            <button
              onClick={toggleAuth}
              className="absolute top-3 right-3 text-gray-600 hover:text-gray-800"
            >
              ✖
            </button>
            <Auth />
          </div>
        </div>
      )}
    </div>
  );
}
